
/* CSS */

.custom-bg-white {
 background-color: white;
}
.button-7 {
  background-color: #4267B2;
  width: 100%;
  border: 1px solid transparent;
  border-radius: 30px;
  box-shadow: rgba(255, 255, 255, .4) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI","Liberation Sans",sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0;
  outline: none;
  padding: 8px .8em;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

.button-7:hover,
.button-7:focus {
  background-color: #173d8a;
}

.button-7:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}tionDetails

.button-7:active {
  background-color: #4267B2;
  box-shadow: none;
}

/* Add this to your CSS file or style block */
.custom-container {
    min-height: 80vh; 
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
  }

  .btn-custum {
    border-radius: 30px;
  }
  .btn-form {
    border-radius: 30px;
  }

  .text-light-custom {
    font-family: 'Roboto', sans-serif;
    color: #c5c5c5 !important;
  }
    
  @media (max-width: 330px) {
    .btn-form {
      border-radius: 11px !important;
      padding: 3px;
      font-size: small;
      font-family: 'Roboto', sans-serif;
    }
  }
    
  @media (min-width: 330px) and (max-width: 391px)  {
    .btn-form {
      border-radius: 13px !important;
      padding: 3px 5px;
      font-size: smaller;
      font-family: 'Roboto', sans-serif;
    }
  }
  
  @media (min-width: 391px) {
    .btn-form {
      border-radius: 15px !important;
      padding: 4px 7px;
      font-family: 'Roboto', sans-serif;
    }
  }
  @media (min-width: 576px) {
    .custom-container {
      max-width: 540px;
    }
  }

  @media (min-width: 768px) {
    .custom-container {
      max-width: 720px;
    }
  }
  
  @media (min-width: 992px) {
    .custom-container {
      max-width: 960px;
    }
  }
  
  @media (min-width: 1200px) {
    .custom-container {
      max-width: 1140px;
    }
  }
  